.ContactBackground{
    background-size: cover;
    background: linear-gradient(120deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    height:100vh;
    min-width:fit-content;
    width:100vw;
}
.ContactContainer{
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap:50px;
    height:95vh;
    padding-top: 5vh;

}
.ContactLeft{
    width:50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
}
.ContactRight{
    width:50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:100%;
}
.ContactGridBox{
    width:fit-content;
    display:grid;
    grid-template-rows: 200px 200px ;
    grid-template-columns: 200px ;
    grid-gap: 5vw;
    object-fit:contain;
}
form.ContactForm{
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 25px;
}
input.NameInput{
    
    padding: 20px;
    background-color: #e8e6e6;
    border: none;
    border-radius: 5px;
    font-size: 20px;
}
input.EmailInput{
    padding: 20px;
    background-color: #e8e6e6;
    border: none;
    border-radius: 5px;
    font-size: 20px;
}
textarea.Email{
    padding: 20px;
    background-color: #e8e6e6;
    border: none;
    border-radius: 5px;
    font-size: 20px;
}

button.SendEmail{
    background-color: #da4ea2;
    color: white;
    border: none;
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
    padding: 20px;
}
img.gridImage{
    scale: 100%;
    resize:contain;
    display: flex;
    height:100%;
    width:100%;

}

h1.ContactTitle{
    color:white;
}